import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DetailService } from '@app/core/http/detail.service';
import { AnimationOptions } from 'ngx-lottie';

@Component({
  selector: 'app-vocab-board',
  templateUrl: './vocab-board.component.html',
  styleUrls: ['./vocab-board.component.css']
})
export class VocabBoardComponent implements OnInit {

  public moduleList: any = [];

  options: AnimationOptions = {
    path: 'assets/icon/lottie/questions-pen.json',
  };

  constructor(private _activeRoute: ActivatedRoute, private _service: DetailService) { }

  ngOnInit() {
    this.checkType();
  }

  checkType() {
   
      this._service.getCheckProgressVocab().subscribe((res) => {
        this.moduleList = res.questionBank;
      })
    
  }

}
