import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VocabListComponent } from './vocab-list/vocab-list.component';
import { RouterModule, Routes } from '@angular/router';
import { VocabBoardComponent } from './vocab-board/vocab-board.component';
import { BasicModule } from '@app/shared/basic-components/basic.module';
import { LottieModule } from 'ngx-lottie';
import player from 'lottie-web';

const routes: Routes = [
  {
    path: 'vocabulary/:qcode',
    component: VocabListComponent,
  },
  {
    path: 'vocabulary',
    component: VocabBoardComponent,
  },
  
]

export function playerFactory() {
  return player;
}

@NgModule({
  declarations: [VocabListComponent, VocabBoardComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicModule,
    LottieModule.forRoot({ player: playerFactory }),
  ],
  providers:[
  ]
})
export class TrainingGameModule { }
